ROGRAMADO DE TODOS O UNA PARTE DE LOS SERVICIOS POR CUALQUIER MOTIVO; (B) EL COSTO DE ADQUISICIÓN DE BIENES O SERVICIOS SUSTITUTOS; (C) CUALQUIER INVERSIÓN, GASTO O COMPROMISO DE SU PARTE EN RELACIÓN CON ESTE ACUERDO O SU USO O ACCESO A LAS OFERTAS DE SERVICIO; O (D) CUALQUIER ACCESO NO AUTORIZADO A, ALTERACIÓN O ELIMINACIÓN, DESTRUCCIÓN, DAÑO, PÉRDIDA O FALTA DE ALMACENAMIENTO DE SU CONTENIDO U OTROS DATOS. EN CUALQUIER CASO, EXCEPTO POR LAS OBLIGACIONES DE PAGO CONFORME A LA SECCIÓN 9.2, NUESTRA Y NUESTROS AFILIADOS Y LICENCIADORES LA RESPONSABILIDAD TOTAL DE ESTE ACUERDO NO EXCEDERÁ LA CANTIDAD QUE USTED NOS PAGA REALMENTE BAJO ESTE ACUERDO POR EL SERVICIO QUE AUMENTÓ EL DÉCIMO DÍA DE RECLAMACIÓN ANTES DE QUE SURGIERA LA RESPONSABILIDAD. LAS LIMITACIONES DE ESTA SECCIÓN 11 SE APLICAN ÚNICAMENTE EN LA MEDIDA MÁXIMA PERMITIDA POR LA LEY APLICABLE. ")]),_c('p'),_c('h3',[_vm._v("12. Modificaciones al Acuerdo.")]),_c('p',[_vm._v(" Podemos modificar este Acuerdo (incluidas las Políticas) en cualquier momento mediante la publicación de una versión revisada en el Sitio DRPMéxico o notificándole de otra manera de acuerdo con la Sección 13.10; siempre que, sin embargo, proporcionemos un aviso con al menos 90 días de anticipación de acuerdo con la Sección 13.10 para cambios adversos en cualquier Acuerdo de Nivel de Servicio. Sujeto al requisito de notificación con 90 días de anticipación con respecto a cambios adversos en los Acuerdos de nivel de servicio, los términos modificados entrarán en vigor al ser publicados o, si le notificamos por correo electrónico, como se indica en el mensaje de correo electrónico. Al continuar utilizando las Ofertas de Servicios después de la fecha de vigencia de cualquier modificación a este Acuerdo, usted acepta estar sujeto a los términos modificados. Es su responsabilidad revisar el sitio DRPMéxico con regularidad para ver si hay modificaciones a este Acuerdo. Modificamos este Acuerdo por última vez en la fecha indicada al final de este Acuerdo. ")]),_c('p'),_c('h3',[_vm._v("13. Varios.")]),_c('p',[_vm._v(" 13.1 "),_c('strong',[_vm._v("Cesión")]),_vm._v(". No cederá ni transferirá este Acuerdo ni ninguno de sus derechos y obligaciones en virtud de este Acuerdo sin nuestro consentimiento previo por escrito. Cualquier cesión o transferencia que viole esta Sección 13.1 será nula. Podemos ceder este Acuerdo sin su consentimiento (a) en relación con una fusión, adquisición o venta de todos o sustancialmente todos nuestros activos, o (b) a cualquier filial o como parte de una reorganización corporativa; y con vigencia a partir de dicha cesión, el cesionario se considera sustituido por DRPMéxico como parte de este Acuerdo y DRPMéxico queda totalmente liberado de todas sus obligaciones y deberes a realizar en virtud de este Acuerdo. Sujeto a lo anterior, este Acuerdo será vinculante y redundará en beneficio de las partes y sus respectivos sucesores y cesionarios autorizados. ")]),_c('p'),_c('p',[_vm._v(" 13.2 "),_c('strong',[_vm._v("Acuerdo completo")]),_vm._v(". Este Acuerdo incorpora las Políticas por referencia y es el acuerdo completo entre usted y nosotros con respecto al tema de este Acuerdo. Este Acuerdo reemplaza todas las representaciones, entendimientos, acuerdos o comunicaciones anteriores o contemporáneos entre usted y nosotros, ya sean escritos o verbales, con respecto al tema de este Acuerdo. No estaremos obligados ni objetaremos específicamente a ningún término, condición u otra disposición que sea diferente o adicional a las disposiciones de este Acuerdo (ya sea que altere materialmente este Acuerdo o no) incluyendo, por ejemplo, cualquier término, condición u otra disposición (a) presentada por usted en cualquier orden, recibo, aceptación, confirmación, correspondencia u otro documento, (b) relacionado con cualquier registro en línea, respuesta a cualquier Solicitud de Oferta, Solicitud de Propuesta, Solicitud de Información u otro cuestionario, o (c) relacionado con cualquier proceso de fac